import { styled } from "@rp/react/theme"
import { useDeviceInfo } from "@rp/react/web/common/hooks"
import { Chip, Popover, Typography } from "@rp/react/web/mui/core"

const ChipWrapper = styled("div", { name: "ChipWrapper" })(({ theme }) => {
  const { isMobile } = useDeviceInfo()
  return {
    display: "flex",
    flexWrap: isMobile ? "nowrap" : "wrap",
    flexGrow: 1,
    alignItems: "center",
    maxWidth: isMobile ? "" : "100%",
    marginLeft: isMobile ? theme.spacing(2) : "0px",
    gap: 4,
  }
})

const BaseChipStyled = styled(Chip, { name: "ChipStyled" })<{ selected: boolean }>(
  ({ theme, selected }) => {
    const { isMobile } = useDeviceInfo()
    return {
      margin: "4px 0px",
      paddingRight: theme.spacing(0.5),
      color: theme.palette.text.primary,
      backgroundColor: selected ? theme.palette.primary.background : theme.palette.overlay.surface,
      minWidth: 0,
      maxWidth: "fit-content",
      width: "auto",
      flexBasis: "300px",
      flexGrow: 1,
      border: "0px",
      fontSize: isMobile ? "18px" : "14px",
      height: isMobile ? "32px" : "24px",
      borderRadius: "4px",
      transition: "background-color 250ms",
      "& .MuiChip-label": {
        display: "flex",
        flexGrow: 0,
        flexWrap: "nowrap",
        lineHeight: "26px",
        alignItems: "center",
      },
      "& .MuiChip-label p": {
        fontSize: isMobile ? "18px" : "14px",
        lineHeight: "26px",
      },
      "&:hover, &:focus": {
        backgroundColor: `${theme.palette.primary.background} !important`,
      },
      "& .MuiChip-deleteIcon ": {
        color: theme.palette.text.secondary,
      },
      "& p": {
        display: "inline",
      },
    }
  },
)
const ChipLabel = styled("span")(() => ({
  textOverflow: "ellipsis",
  whiteSpace: "nowrap",
  overflow: "hidden",
  maxWidth: "500px",
}))

const TotalDisplay = styled(Typography)(() => ({
  marginLeft: 5,
  marginRight: 2,
  color: "#ffffff99",
}))

const Separator = () => <Typography style={{ margin: "0 3px" }}>|</Typography>

const PopoverStyled = styled(Popover)(({ theme }) => {
  const { isMobile } = useDeviceInfo()
  return {
    ".MuiPopover-paper": {
      borderRadius: "16px",
      marginTop: theme.spacing(0.5),
      width: isMobile ? "100%" : undefined,
    },
  }
})

export { ChipWrapper, BaseChipStyled, ChipLabel, TotalDisplay, Separator, PopoverStyled }
